import * as THREE from "three";
import { LightProbeGenerator } from "three/examples/jsm/lights/LightProbeGenerator";

import { HdriPink } from "./Materials";
import Gallery from "./Models/Gallery";
import Ocean from "./Ocean";

// Post-processing
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";
// import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
// import { SAOPass } from 'three/examples/jsm/postprocessing/SAOPass';

// Controls
import FPControls from "./FPControls";
import { LightProbe } from "three";

export default class WebGLView {
  constructor(app, canvas) {
    //Always define reference to the main app for this class context
    this.app = app;

    this.maxPixelRatio = 2;
    this.deltaTime = 0;
    this.maxDeltaTime = 1 / 15;

    this.updateListeners = [];

    this.initThree(canvas);
    this.initInteractions();
    this.initEnvironment();
    this.initModels();
    this.initPostProcessing();
  }

  initThree(canvas) {
    // Scene
    this.scene = new THREE.Scene();

    // Camera
    this.camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    this.camera.position.set(0, 2, 0);

    // Renderer
    this.renderer = new THREE.WebGLRenderer({ canvas: canvas });
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setClearColor("#000");

    // Post-processing
    this.composer = new EffectComposer(this.renderer);
    this.composer.addPass(new RenderPass(this.scene, this.camera));

    // Timing
    this.clock = new THREE.Clock(true);
  }

  initInteractions() {
    this.fpControls = new FPControls(this.camera, this.renderer.domElement);
    this.updateListeners.push(this.fpControls);
  }

  initEnvironment() {
    // Skybox
    this.scene.background = HdriPink;

    // Environment Map
    this.envmap = new THREE.CubeTextureLoader()
      .setPath("textures/envmap/studio05/")
      .load(
        [
          // "px.jpg", // right
          // "nx.jpg", // left
          // "py.jpg", // up
          // "ny.jpg", // down
          // "pz.jpg", // back
          // "nz.jpg", // fwd
          "px.png", // right
          "nx.png", // left
          "py.png", // up
          "ny.png", // down
          "pz.png", // back
          "nz.png", // fwd
        ],
        (envmap) => {
          this.scene.environment = envmap;

          this.scene.lightProbe = LightProbeGenerator.fromCubeTexture(envmap);
          // this.scene.lightProbe.intensity = 0.75;
          this.scene.add(this.scene.lightProbe);
        }
      );

    // Fog
    // this.scene.fog = new THREE.FogExp2(0xcbaecd, 0.025);

    // Lighting
    // const hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.5);
    // this.scene.hemiLight = hemiLight;
    // this.scene.add(hemiLight);

    // const lightDirKey = new THREE.DirectionalLight(0xffffff, 0.15);
    // lightDirKey.position.set(6, 10, 9);
    // lightDirKey.position.multiplyScalar(50);
    // this.scene.lightDirKey = lightDirKey;
    // this.scene.add(lightDirKey);
  }

  initModels() {
    const gallery = new Gallery(this);
    this.scene.gallery = gallery;
    this.scene.add(gallery);

    // const ocean = new Ocean(this.scene.lightDirKey, true);
    const ocean = new Ocean(null, true);
    this.scene.ocean = ocean;
    ocean.position.y = 0;
    this.scene.add(ocean);
    this.updateListeners.push(ocean);
  }

  initPostProcessing() {
    // BLOOM
    const bloomPass = new UnrealBloomPass(
      new THREE.Vector2(window.innerWidth, window.innerHeight),
      0.4, // strength 0.3
      0, // radius 0
      0.95 // threshold 0.9
    );
    bloomPass.renderToScreen = true;
    this.bloomPass = bloomPass;
    this.composer.addPass(bloomPass);
  }

  //
  // PUBLIC RENDER
  //
  update() {
    this.deltaTime = Math.min(this.maxDeltaTime, this.clock.getDelta()) / 1000;
    for (const updatable of this.updateListeners) {
      if (updatable.update) {
        updatable.update(this.deltaTime);
      }
    }
  }

  render() {
    this.composer.render(this.deltaTime);
  }

  //
  // CONVENIENCE
  //
  get width() {
    return window.innerWidth;
  }

  get height() {
    return window.innerHeight;
  }

  get pixelRatio() {
    const pRatio = Math.min(this.maxPixelRatio, window.devicePixelRatio);
    return pRatio < this.maxPixelRatio ? pRatio : this.maxPixelRatio;
  }

  //
  // EVENT HANDLERS
  //

  resize() {
    if (!this.renderer) return;

    this.camera.aspect = window.innerWidth / window.innerHeight;
    this.camera.updateProjectionMatrix();

    /* Use this for resizing scene
    this.fovHeight =
      2 *
      Math.tan((this.camera.fov * Math.PI) / 180 / 2) *
      this.camera.position.z;
    const scale = this.webgl.fovHeight / this.height;
      */

    if (this.renderer.getPixelRatio() !== this.pixelRatio) {
      this.renderer.setPixelRatio(this.pixelRatio);
    }
    this.renderer.setSize(window.innerWidth, window.innerHeight);
    this.composer.setSize(
      this.pixelRatio * this.width,
      this.pixelRatio * this.height
    );

    // if (this.interactive) this.interactive.resize();
  }
}
