import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js";

import {
  MAT_NeonWhite,
  MAT_Flower,
  MAT_Building,
  MAT_StairPillar,
  MAT_EHJwall,
  MAT_EHJinlay,
  // Crystal
  MAT_CrystalHead,
  MAT_CrystalWall,
  MAT_CrystalSnowFlake,
  MAT_CrystalFloor,
  // MACover
  MAT_MacoverHero,
  MAT_MacoverP1,
  MAT_MacoverP2,
  MAT_MacoverP3,
  MAT_MacoverP4,
  // Mirror
  MAT_Plant,
  MAT_Glass,
  MAT_Mirror,
  MAT_NeonPink,
  MAT_LightWhite,
  MAT_Marble,
  MAT_MatteBlack,
  MAT_WhiteSteel,
  MAT_SteelFrame,
  MAT_WhiteConcrete,
  // thumbnails
  MAT_TNdisability,
  MAT_TNehjMain,
  MAT_TNehjMini,
  MAT_TNfruity,
  MAT_TNtoday,
  MAT_TNtogether,
} from "../Materials";

export default class Gallery extends THREE.Group {
  constructor(webgl) {
    super();

    const loader = new GLTFLoader();

    const dracoLoader = new DRACOLoader();
    dracoLoader.setDecoderPath("libs/");
    dracoLoader.preload();
    loader.setDRACOLoader(dracoLoader);

    loader.load("models/Gallery.glb", (gltf) => {
      this.galleryGltf = gltf;
      this.add(this.galleryGltf.scene);
      dracoLoader.dispose();

      this.galleryGltf.scene.traverse((child) => {
        // SET MATERIALS
        if (child.material)
          switch (child.material.name) {
            case "Neon":
              child.material = MAT_NeonWhite;
              break;
            case "MAT_Flower":
              child.material = MAT_Flower;
              break;
            // case 'Building':
            //   child.material = MAT_Building;
            //   break;
            case "MAT_StairPillar":
              child.material = MAT_StairPillar;
              break;
            // EHJ
            case "M_EHJwall":
              child.material = MAT_EHJwall;
              break;
            case "M_EHJinlay":
              child.material = MAT_EHJinlay;
              break;
            // Crystal
            case "M_CrystalHead":
              child.material = MAT_CrystalHead;
              break;
            case "M_CrystalWall":
              child.material = MAT_CrystalWall;
              break;
            case "M_CrystalFloor":
              child.material = MAT_CrystalFloor;
              break;
            case "MAT_Snowflake_Glass":
              child.material = MAT_CrystalSnowFlake;
              break;
            // MACover
            case "MACoverHero":
              child.material = MAT_MacoverHero;
              break;
            case "MACover1":
              child.material = MAT_MacoverP1;
              break;
            case "MACover2":
              child.material = MAT_MacoverP2;
              break;
            case "MACover3":
              child.material = MAT_MacoverP3;
              break;
            case "MACover4":
              child.material = MAT_MacoverP4;
              break;
            // MIRROR
            case "MAT_Mirror_Glass":
              child.material = MAT_Glass;
              break;
            case "MAT_neon_pink":
              child.material = MAT_NeonPink;
              break;
            case "MAT_light":
              child.material = MAT_LightWhite;
              break;
            case "MAT_mirror":
              child.material = MAT_Mirror;
              break;
            case "MAT_plants":
              child.material = MAT_Plant;
              break;
            case "MAT_whiteconcrete":
              child.material = MAT_WhiteConcrete;
              break;
            case "MAT_steelframe":
              child.material = MAT_SteelFrame;
              break;
            case "MAT_whitesteel":
              child.material = MAT_WhiteSteel;
              break;
            case "MAT_marble":
              child.material = MAT_Marble;
              break;
            case "MAT_matteblack":
              child.material = MAT_MatteBlack;
              break;
          }
        if (child.name) {
          switch (child.name) {
            case "ScreenFruity":
              child.material = MAT_TNfruity;
              break;
            case "ScreenTogether":
              child.material = MAT_TNtogether;
              break;
            case "ScreenToday":
              child.material = MAT_TNtoday;
              break;
            case "ScreenDisability":
              child.material = MAT_TNdisability;
              break;
            case "ScreenEHJmain":
              child.material = MAT_TNehjMain;
              break;
            case "ScreenEHJp1":
              child.material = MAT_TNehjMini;
              break;
            case "ScreenEHJp2":
              child.material = MAT_TNehjMini;
              break;
            case "ScreenEHJp3":
              child.material = MAT_TNehjMini;
              break;
            case "ScreenEHJp4":
              child.material = MAT_TNehjMini;
              break;
          }
        }
        // REGISTER INTERACTABLES
        if (child.name) {
          if (child.name.includes("Menu")) {
            // webgl.interactables.push(child);
          } else if (child.name.includes("Pedestal")) {
            // webgl.interactables.push(child);
          }
        }
      });
    });
  }
}
