//
// UTILITY FUNCTIONS
//
function loadTexture(url, renderer) {
  const texture = new THREE.TextureLoader().load(url);
  texture.flipY = false;
  if (renderer) {
    // Force texture to be uploaded to GPU immediately
    // to avoid "jank" on first rendered frame
    renderer.initTexture(texture);
  }
  return texture;
}
