import DebugUI from "./gui/DebugUI.js";
import WebGLView from "./webgl/WebGLView.js";

export default class App {
  constructor() {}

  init() {
    this.initDebugUI();
    this.initWebGLView();
    this.addListeners();
    this.animate();
    this.resize();
  }

  initDebugUI() {
    this.debugUI = new DebugUI(this, true);
  }

  initWebGLView() {
    this.webgl = new WebGLView(this, document.querySelector("#threejs"));
  }

  addListeners() {
    this.handlerAnimate = this.animate.bind(this);
    this.webgl.renderer.setAnimationLoop(this.handlerAnimate);

    this.handlerResize = this.resize.bind(this);
    window.addEventListener("resize", this.handlerResize);
    window.addEventListener("orientationchange", this.handlerResize);
  }

  //
  // PUBLIC
  //
  animate() {
    this.update();
    this.render();
  }

  update() {
    if (this.webgl) this.webgl.update();
    if (this.debugUI) this.debugUI.update();
  }

  render() {
    if (this.webgl) this.webgl.render();
    if (this.debugUI) this.debugUI.render();
  }

  //
  // EVENT HANDLERS
  //
  resize() {
    if (this.webgl) this.webgl.resize();
    if (this.debugUI) this.webgl.resize();
  }
}
