import { Vector3, Euler, DynamicCopyUsage } from "three";

const PI_2 = Math.PI / 2;

export default class FPControls {
  constructor(camera, domElement) {
    this.camera = camera;
    this.domElement = domElement;
    this.euler = new Euler(0, 0, 0, "YXZ");
    this.vec = new Vector3();
    this.velocity = new Vector3();
    this.direction = new Vector3();

    this.velocityDamp = 10;
    this.velocityScale = 400;

    this.isLocked = false;
    this.moveForward = false;
    this.moveBackward = false;
    this.moveLeft = false;
    this.moveRight = false;
    this.moveUp = false;
    this.moveDown = false;

    window.addEventListener("keydown", this.onKeyDown.bind(this), false);
    window.addEventListener("keyup", this.onKeyUp.bind(this), false);
    this.domElement.addEventListener(
      "mousemove",
      this.onMouseMove.bind(this),
      false
    );
  }

  onMouseMove(event) {
    // if ( this.isLocked === false ) return;

    const movementX =
      event.movementX || event.mozMovementX || event.webkitMovementX || 0;
    const movementY =
      event.movementY || event.mozMovementY || event.webkitMovementY || 0;

    this.euler.setFromQuaternion(this.camera.quaternion);

    this.euler.y -= movementX * 0.002;
    this.euler.x -= movementY * 0.002;

    this.euler.x = Math.max(-PI_2, Math.min(PI_2, this.euler.x));

    this.camera.quaternion.setFromEuler(this.euler);
  }

  onKeyDown(event) {
    switch (event.keyCode) {
      case 87:
        /*W*/ this.moveForward = true;
        break;
      case 65:
        /*A*/ this.moveLeft = true;
        break;
      case 83:
        /*S*/ this.moveBackward = true;
        break;
      case 68:
        /*D*/ this.moveRight = true;
        break;
      case 69:
        /*E*/ this.moveUp = true;
        break;
      case 81:
        /*Q*/ this.moveDown = true;
        break;
      case 32:
        /*SPACE*/ this.logCamera();
        break;
    }
  }

  onKeyUp(event) {
    switch (event.keyCode) {
      case 87:
        /*W*/ this.moveForward = false;
        break;
      case 65:
        /*A*/ this.moveLeft = false;
        break;
      case 83:
        /*S*/ this.moveBackward = false;
        break;
      case 68:
        /*D*/ this.moveRight = false;
        break;
      case 69:
        /*E*/ this.moveUp = false;
        break;
      case 81:
        /*Q*/ this.moveDown = false;
        break;
    }
  }

  // TODO: fix the shittiness here
  // velocity is signed incorrectly
  // variables are named confusingly
  update(dt) {
    // if (this.isLocked === false) return;
    dt *= 1000;

    this.velocity.x -= this.velocity.x * this.velocityDamp * dt;
    this.velocity.z -= this.velocity.z * this.velocityDamp * dt;
    this.velocity.y -= this.velocity.y * this.velocityDamp * dt;

    this.direction.z = Number(this.moveBackward) - Number(this.moveForward);
    this.direction.x = Number(this.moveRight) - Number(this.moveLeft);
    this.direction.y = Number(this.moveUp) - Number(this.moveDown);
    this.direction.normalize(); // this ensures consistent movements in all directions

    if (this.moveForward || this.moveBackward)
      this.velocity.z -= this.direction.z * this.velocityScale * dt;
    if (this.moveLeft || this.moveRight)
      this.velocity.x -= this.direction.x * this.velocityScale * dt;
    if (this.moveUp || this.moveDown)
      this.velocity.y -= this.direction.y * this.velocityScale * dt;

    this.moveLR(-this.velocity.x * dt);
    this.moveFB(-this.velocity.z * dt);
    this.moveUD(-this.velocity.y * dt);
  }

  moveLR(distance) {
    // this.vec.setFromMatrixColumn(this.camera.matrix, 0);
    // this.camera.position.addScaledVector(this.vec, distance);
    this.camera.translateX(distance);
  }

  moveFB(distance) {
    // this.vec.setFromMatrixColumn(this.camera.matrix, 0);
    // this.vec.crossVectors(this.camera.up, this.vec);
    // this.camera.position.addScaledVector(this.vec, distance);
    this.camera.translateZ(distance);
  }

  moveUD(distance) {
    // this.vec.setFromMatrixColumn(this.camera.matrix, 0);
    // this.vec.crossVectors(this.camera.position, this.vec);
    // this.camera.position.addScaledVector(this.vec, distance);
    this.camera.translateY(distance);
  }

  // getDirection() {
  //   const direction = new Vector3(0, 0, -1);

  //   return direction.applyQuaternion(this.camera.quaternion);
  // }

  logCamera() {
    console.log(
      "camera position: THREE.Vector3(" +
        this.camera.position.x +
        ", " +
        this.camera.position.y +
        ", " +
        this.camera.position.z +
        ")"
    );
    console.log(
      "camera rotation: THREE.Euler(" +
        this.camera.rotation.x +
        ", " +
        this.camera.rotation.y +
        ", " +
        this.camera.rotation.z +
        "'XYZ')"
    );
  }
}
